<template>
  <div>
    <div style="margin-bottom:60px; margin-top:60px">
      <h1>registro</h1>
    </div>
    <v-form
      ref="form"
      class="mt-5 mb-4"
      v-model="valid"
      lazy-validation
      >
      <v-text-field
        v-model="email"
        label="Email"
        type="email"
        dense
        outlined
        :rules="emailRules"
        >
      </v-text-field>
      <v-text-field
        v-model="password"
        :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
        :type="show1 ? 'text' : 'password'"
        label="Contraseña"
        dense
        outlined
        :rules="passwordRules"
        @click:append="show1 = !show1"
        class="mt-2"
        >
      </v-text-field>
      <v-text-field
        v-model="password2"
        :append-icon="show2 ? 'mdi-eye' : 'mdi-eye-off'"
        :type="show2 ? 'text' : 'password'"
        label="Confirmar contraseña"
        dense
        outlined
        :rules="passwordRules"
        @click:append="show2 = !show2"
        class="mt-2"
        >
      </v-text-field>
      <div>
        <v-btn @click="register" color="accent" width="70%" :loading="processing">Registrarse</v-btn><br>
      </div>
      <v-divider class="my-4"></v-divider>
        <v-btn @click="$emit('change-mode', 'login')" color="secondary" width="70%">volver</v-btn><br>
    </v-form>
  </div>
</template>
<script>
import { getAuth, createUserWithEmailAndPassword } from 'firebase/auth'
export default {
  data () {
    return {
      processing: false,
      valid: true,
      email: '',
      password: '',
      password2: '',
      show1: false,
      show2: false,
      emailRules: [
        v => !!v || 'Email es requerido.',
        v => /.+@.+\..+/.test(v) || 'Debe ser un email valido.'
      ],
      passwordRules: [
        v => !!v || 'Contraseña es requerido.',
        v => (this.password === this.password2) || 'Las contraseñas deben coincidir.'
      ]
    }
  },
  methods: {
    async register () {
      this.$refs.form.validate()
      console.log('hi')
      if (this.valid) {
        this.processing = true
        try {
          console.log('registering')
          const auth = getAuth()
          console.log(auth)
          await createUserWithEmailAndPassword(auth, this.email, this.password)
        } catch (error) {
          console.log(error)
          const errorCode = error?.code
          console.log(errorCode)
          if (errorCode === 'auth/email-already-in-use') this.$toasted.error('Correo ya esta en uso', { icon: 'error', duration: 3000 })
          else this.$toasted.error('Error en autenticación', { icon: 'error', duration: 3000 })
        }
        this.processing = false
      }
    }
  },
  watch: {
    password (val) {
      this.$refs.form.validate()
    },
    password2 (val) {
      this.$refs.form.validate()
    }
  }
}
</script>
